.howToBakeMain_mobile {
  background-color: #a71814;
  margin-top: 4.5em;
  position: relative;
}

.howToBakeTitle_mobile {
  font-size: 24px;
  text-align: center;
  color: #fffad4;
  margin: 0;
  padding-top: 30px;
}

.howToBakeUnderline_mobile {
  width: 200px;
  margin: auto;
  display: block;
}

.howToBakeVideoContainer_mobile {
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  padding: 20px 0;
}

.bakingVideoContainer_mobile {
  width: 80%;
  position: relative;
}

#bakingVideo_mobile {
  width: 100%;
  height: auto;
}

.bakingVideoFrame_mobile {
  width: 102%;
  height: 110%;
  position: absolute;
  left: -1%;
  top: -8%;
}

.backgroundUnderline_mobile {
  width: 100%;
  position: absolute;
  bottom: -1%;
  left: 0;
}

.howToBakeInstructionContainer_mobile {
  color: #fffad4;
  margin: 0 40px;
}

.howToBakeStep1_mobile,
.howToBakeStep2_mobile,
.howToBakeStep3_mobile {
  display: flex;
}

.howToBakeStep1InstructionImageContainer {
  width: 50%;
}

.howToBakeStep2InstructionImageContainer {
  width: 90%;
}

.howToBakeStep3InstructionImageContainer {
  width: 130%;
}

.howToBakeStep1ImageContainer_mobile {
  width: 8%;
  margin-top: 10%;
  margin-right: 10px;
}

.howToBakeStep2ImageContainer_mobile {
  width: 40%;
  margin-top: 10%;
  margin-right: 10px;
}

.howToBakeStep3ImageContainer_mobile {
  width: 40%;
  margin-top: 10%;
  margin-right: 10px;
}

.howToBakeStep1InstructionImageContainer,
.howToBakeStep2InstructionImageContainer,
.howToBakeStep3InstructionImageContainer {
  align-self: center;
}

@media (min-width: 320px) {
  .howToBakeStep1ImageContainer_mobile {
    margin-top: 8%;
    margin-left: 10px;
  }
}

@media (min-width: 360px) {
  .howToBakeStep1ImageContainer_mobile {
    width: 15px;
    margin-top: 20px;
  }

  .howToBakeStep2ImageContainer_mobile {
    width: 80px;
    margin-top: 20px;
  }

  .howToBakeStep3ImageContainer_mobile {
    width: 95px;
    margin-top: 20px;
  }

  .howToBakeStep1InstructionImageContainer {
    width: 30%;
  }

  .howToBakeStep2InstructionImageContainer {
    width: 50%;
  }

  .howToBakeStep3InstructionImageContainer {
    width: 80%;
  }
}

@media (min-width: 375px) {
  .howToBakeMain_mobile {
    margin-top: 5em;
  }

  .howToBakeInstructionContainer_mobile {
    color: #fffad4;
    margin: 0 40px;
    padding-bottom: 40px;
  }
}

@media (min-width: 540px) {
  .howToBakeStep1ImageContainer_mobile {
    width: 14px;
    margin-top: 20px;
  }

  .howToBakeStep2ImageContainer_mobile {
    width: 50px;
    margin-top: 20px;
  }

  .howToBakeStep3ImageContainer_mobile {
    width: 60px;
    margin-top: 20px;
  }

  .howToBakeStep1InstructionImageContainer {
    width: 20%;
  }

  .howToBakeStep2InstructionImageContainer {
    width: 23%;
  }

  .howToBakeStep3InstructionImageContainer {
    width: 30%;
  }

  .backgroundUnderline_mobile {
    width: 100%;
    position: absolute;
    bottom: -2%;
    left: 0;
  }
}

@media (min-width: 768px) {
  .howToBakeMain_mobile {
    margin-top: 6em;
  }
  .howToBakeTitle_mobile {
    font-size: 36px;
  }

  .howToBakeUnderline_mobile {
    width: 300px;
  }

  .howToBakeStep1ImageContainer_mobile {
    width: 18px;
    margin-top: 30px;
  }

  .howToBakeStep2ImageContainer_mobile {
    width: 55px;
    margin-top: 30px;
  }

  .howToBakeStep3ImageContainer_mobile {
    width: 65px;
    margin-top: 30px;
  }

  .howToBakeStep2InstructionImageContainer {
    width: 22%;
  }

  .howToBakeInstructionContainer_mobile {
    font-size: 25px;
  }
}
