@media (min-width: 1024px) {
  .howToBakeMain {
    margin-top: 4.5em;
    background-color: #a71814;
    position: relative;
  }

  .backgroundUnderline {
    width: 100%;
    position: absolute;
    bottom: -4%;
    left: 0;
  }

  .howToBakeTitle {
    padding-top: 30px;
    font-size: 24px;
    text-align: center;
    color: #fffad4;
    margin: 0;
  }

  .howToBakeUnderline {
    width: 200px;
    margin: auto;
    display: block;
  }

  .howToBakeMain {
    margin-top: 4em;
  }

  .howToBakeTitle {
    font-size: 50px;
    padding-top: 100px;
  }

  .howToBakeUnderline {
    width: 400px;
    margin-bottom: 1%;
  }

  .howToBakeVideoContainer {
    width: 100%;
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
  }

  .bakingVideoContainer {
    width: 50%;
    margin: 20px 100px 20px 0;
    position: relative;
  }

  #bakingVideo {
    width: 100%;
    height: auto;
  }

  .instructionIconsContainer {
    width: 7%;
    display: flex;
    align-items: center;
  }

  .bakingVideoFrame {
    width: 102%;
    height: 114%;
    position: absolute;
    left: -1%;
    top: -8.6%;
  }

  .howToBakeInstructionContainer {
    display: flex;
    justify-content: space-around;
    width: 70%;
    margin: auto;
    padding-bottom: 50px;
  }

  .howToBakeStep1ImageContainer {
    margin-top: 25px;
    width: 28%;
    margin-right: 10px;
  }
  .howToBakeStep2ImageContainer {
    margin-top: 25px;
    width: 40%;
    margin-right: 10px;
  }
  .howToBakeStep3ImageContainer {
    margin-top: 25px;
    width: 40%;
    margin-right: 10px;
  }

  .howToBakeStep1 {
    flex-basis: 15%;
    font-size: 18px;
    color: #fffad4;
    display: flex;
  }
  .howToBakeStep2 {
    flex-basis: 30%;
    font-size: 18px;
    color: #fffad4;
    display: flex;
  }
  .howToBakeStep3 {
    flex-basis: 30%;
    font-size: 18px;
    color: #fffad4;
    display: flex;
  }
}

@media (min-width: 1280px) {
  .howToBakeTitle {
    font-size: 62px;
  }

  .howToBakeUnderline {
    width: 500px;
  }
  .howToBakeStep1ImageContainer {
    width: 18%;
  }
  .howToBakeStep2ImageContainer {
    width: 25%;
  }
  .howToBakeStep3ImageContainer {
    width: 25%;
  }
  .backgroundUnderline {
    bottom: -4.5%;
  }
}

@media (min-width: 1600px) {
  .howToBakeStep1 {
    flex-basis: 20%;
    font-size: 22px;
  }
  .howToBakeStep2 {
    flex-basis: 23%;
    font-size: 22px;
  }
  .howToBakeStep3 {
    flex-basis: 25%;
    font-size: 22px;
  }

  .howToBakeStep1ImageContainer {
    width: 6%;
    margin-top: 30px;
  }
  .howToBakeStep2ImageContainer {
    width: 30%;
    margin-top: 30px;
  }
  .howToBakeStep3ImageContainer {
    width: 28%;
    margin-top: 30px;
  }

  .backgroundUnderline {
    bottom: -5%;
  }
}
